.calcApp {
  margin: 2em 2em;
  background: rgb(40, 43, 68);
  border-radius: 1em;
  overflow: hidden;
  box-shadow: 0px 10px 10px 5px rgba(0, 0, 0, 0.4);
}

.displayResult,
.displayQuery {
  margin: 0 !important;
}

.displayQuery {
  font-size: 1.5rem;
}

.displayResult {
  font-size: 4rem;
}

.display {
  margin: 0;
  padding: 1em 2em;
  text-align: right;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: "digital display tfb" !important;
  font-size: 16px;
  font-style: normal;
  background: rgba(0, 0, 0, 0.3);
  color: whitesmoke;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

.btnPanel {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  padding: 10px;
  min-height: 500px;
}

.calcBtn {
  font-family: "Ubuntu", sans-serif;
  outline: none;
  font-size: 2rem;
  border: none;
  border-radius: 10px;
  background: rgb(218, 218, 218);
}

.calcBtn:hover {
  cursor: pointer;
  -webkit-filter: brightness(120%);
          filter: brightness(120%);
}

.calcBtn:active {
  transform: translateY(1px);
}

.calcBtn:focus {
  outline: none;
}

.operator {
  background: #4b778d;
  color: white;
  font-size: 2.5rem;
  font-weight: bold;
}

.decimal,
.clear {
  font-weight: bold;
}

.clear {
  background: rgb(122, 9, 9);
  color: white;
}

.equalSign {
  grid-column: -2;
  grid-row: 2 / span 4;
  font-weight: bold;
  background: #558776;
}

.pane {
  height: 50vh;
  display: flex;
  text-align: left;
}

.editorContainer {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
}

.editorContainer.collapsed {
  flex-grow: 0;
}

.editorContainer.collapsed .CodeMirror-scroll {
  position: absolute;
  overflow: hidden !important;
}

.editorHead {
  display: flex;
  justify-content: space-between;
  background: rgba(0, 0, 0, 0.3);
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  border-radius: 0.5rem 0.5rem 0 0;
}

.collapseBtn {
  cursor: pointer;
  margin-left: 0.5rem;
  background: none;
  border: none;
  min-width: 1.5rem;
}

.collapseBtn img {
  height: 1rem;
  width: 1rem;
}

.CodeMirror {
  height: 100% !important;
}

.codeMirrorWrapper {
  flex-grow: 1;
  border-radius: 0 0 0.5rem 0.5rem;
  overflow: hidden;
}

iframe {
  margin: 0 0.5rem;
  border-radius: 0.5rem;
  background: rgb(202, 202, 202);
}

.resumePage {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background-color: whitesmoke;
}

.RBHeader {
  width: 90vw;
  height: 80px;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  z-index: 10;
  transform: translateY(20px);
  border-radius: 40px;
}

.resumeName {
  background: none;
  color: white;
  border: none;
  text-align: left;
  font-weight: bold;
  font-size: 2.5rem;
}

.resumeName:focus {
  border: none;
  outline: none;
}

.resumeDownloadIcon {
  height: 2.5rem;
  cursor: pointer;
}

.resumeDownloadIcon:hover {
  -webkit-filter: brightness(120%);
          filter: brightness(120%);
  transform: scale(110%);
}

.resumeDownloadIcon:active {
  transform: scale(90%);
}

@media screen and (max-width: 800px) {
  .RBHeader {
    padding: 0 20px;
  }
}

.App {
  text-align: center;
}

.center{
  display: flex;
    justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

